import React from 'react';
import { Form } from '../../../shared/useForm';
import Button from '@material-ui/core/Button';
import Input from '../../../components/Controls/Input';
import RadioGroup from '../../../components/Controls/RadioGroup';
import FormHelperText from '@material-ui/core/FormHelperText';
import classes from './EnrollmentForm.module.css';

const studiesItems = [
  { id: 'Licenciatura', title: 'Licenciatura' },
  { id: 'Maestria', title: 'Maestria' },
  { id: 'Doctorado', title: 'Doctorado' },
];

const StudiesForm = (props) => {
  const { values, errors, setErrors, handleInputChange } = props;
  const { studies, collegeCareer } = values;

  const validate = () => {
    let temp = { ...errors };

    studies
      ? (temp.studies = '')
      : (temp.studies = 'Seleccione su grado de estudios');

    collegeCareer
      ? (temp.collegeCareer = '')
      : (temp.collegeCareer = 'Campo obligatorio');

    setErrors({
      ...temp,
    });

    if (values) return Object.values(temp).every((x) => x == '');
  };

  const next = (e) => {
    e.preventDefault();
    props.nextStep();
  };
  const prev = (e) => {
    e.preventDefault();
    props.prevStep();
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (validate()) {
      next(e);
    }
  };

  let form = (
    <Form className={classes.form} onSubmit={handleSubmit}>
      <h4>Estudios</h4>

      <RadioGroup
        name='studies'
        label='Estudios'
        value={studies || ''}
        onChange={handleInputChange}
        items={studiesItems}
      />
      <FormHelperText error>{errors.studies}</FormHelperText>

      <Input
        name='collegeCareer'
        placeholder='Carrera cursada (especificar años y/o meses'
        label='Carrera cursada (especificar años y/o meses'
        value={collegeCareer || ''}
        onChange={handleInputChange}
        inputProps={{ maxLength: 50 }}
        error={errors.collegeCareer}
      />
      <Button
        type='submit'
        fullWidth
        variant='contained'
        color='primary'
        style={{ marginTop: '15px' }}
        onClick={prev}
      >
        Regresar
      </Button>
      <Button
        type='submit'
        fullWidth
        variant='contained'
        color='primary'
        style={{ marginTop: '15px' }}
      >
        Siguiente
      </Button>
    </Form>
  );

  return <div>{form}</div>;
};

export default StudiesForm;
