import React, { useLayoutEffect, useRef } from 'react';
import Grid from '@material-ui/core/Grid';
import { Form } from '../../../shared/useForm';
import Button from '@material-ui/core/Button';
import { List, ListItem, ListItemText } from '@material-ui/core/';
import Spinner from '../../../components/UI/Spinner/Spinner';
import classes from './EnrollmentForm.module.css';

const ReviewForm = (props) => {
  const divRef = useRef(null);

  useLayoutEffect(() => {
    divRef.current.scrollIntoView({ block: 'end', behavior: 'instant' });
  }, []);

  const { values, handleSubmit, loading } = props;
  const {
    name,
    firstLastName,
    secondLastName,
    rfc,
    email,
    phoneNumber,
    plaza,
    course,
    instructor,
    courseDate,
    courseSchedule,
    classroom,
    hours,
  } = values;

  const initialForm = (e) => {
    e.preventDefault();
    props.initialForm();
  };

  let form = (
    <Form className={classes.form} onSubmit={handleSubmit}>
      <h4 ref={divRef}>Confirmar datos</h4>
      <Grid container>
        <Grid item xs={6}>
          <List>
            <ListItem>
              <ListItemText
                primary='Nombre Completo'
                secondary={`${name} ${firstLastName} ${secondLastName}`}
              />
            </ListItem>
            <ListItem>
              <ListItemText primary='RFC' secondary={rfc} />
            </ListItem>
            <ListItem>
              <ListItemText primary='Telefono' secondary={phoneNumber} />
            </ListItem>
            <ListItem>
              <ListItemText primary='Email' secondary={email} />
            </ListItem>
            <ListItem>
              <ListItemText primary='Plaza' secondary={plaza} />
            </ListItem>
          </List>
        </Grid>
        <Grid item xs={6}>
          <List>
            <ListItem>
              <ListItemText primary='Nombre del curso' secondary={course} />
            </ListItem>
            <ListItem>
              <ListItemText
                primary='Nombre del instructor'
                secondary={instructor}
              />
            </ListItem>
            <ListItem>
              <ListItemText primary='Aula' secondary={classroom} />
            </ListItem>
            <ListItem>
              <ListItemText primary='Fecha del curso' secondary={courseDate} />
            </ListItem>
            <ListItem>
              <ListItemText
                primary='Horario del curso'
                secondary={courseSchedule}
              />
            </ListItem>
            <ListItem>
              <ListItemText primary='Duración(horas)' secondary={hours} />
            </ListItem>
          </List>
        </Grid>
      </Grid>
      <Button
        type='submit'
        fullWidth
        variant='contained'
        color='primary'
        style={{ marginTop: '15px' }}
        onClick={initialForm}
      >
        Regresar al inicio
      </Button>
      <Button
        type='submit'
        fullWidth
        variant='contained'
        color='primary'
        style={{ marginTop: '15px' }}
      >
        Confirmar e inscribir
      </Button>
    </Form>
  );

  if (loading) {
    form = <Spinner />;
  }

  return <div>{form}</div>;
};

export default ReviewForm;
