import React, { useState, useLayoutEffect } from 'react';
import { Redirect } from 'react-router-dom';
import PersonalInfoForm from './PersonalInfoForm';
import StudiesForm from './StudiesForm';
import LabourInfoForm from './LabourInfoForm';
import ReviewForm from './ReviewForm';
import Spinner from '../../../components/UI/Spinner/Spinner';
import { Alert, AlertTitle } from '@material-ui/lab';
import axios from 'axios';

export const EnrollmentForm = (props) => {
  const [step, setStep] = useState(1);
  const [errors, setErrors] = useState({});
  const [loading, setLoading] = useState(false);
  const [redirect, setRedirect] = useState(false);
  const [enrolled, setEnrolled] = useState(false);
  const [values, setValues] = useState({
    name: '',
    firstLastName: '',
    secondLastName: '',
    rfc: '',
    email: '',
    plaza: '',
    gender: '',
    department: '',
    studies: '',
    collegeCareer: '',
    positionType: '',
    positionLevel: '',
    responsibleUnit: '',
    area: '',
    actualPosition: '',
    immediateBoss: '',
    address: '',
    phoneNumber: '',
    schedule: '',
    course: '',
    instructor: '',
    courseDate: '',
    courseSchedule: '',
    campus: '',
    classroom: '',
    group: '',
    hours: '',
  });
  const { rowData } = props;

  useLayoutEffect(() => {
    request();
    // eslint-disable-next-line
  }, []);

  const request = () => {
    setLoading((loading) => !loading);

    //let url ='/api/enrollment-form/' + rowData
    axios
      .get('/api/enrollment-form/' + rowData)
      .then((response) => {
        setValues({
          name: response.data.name,
          firstLastName: response.data.firstLastName,
          secondLastName: response.data.secondLastName,
          rfc: response.data.rfc,
          email: response.data.email,
          plaza: response.data.plaza,
          phoneNumber: response.data.phoneNumber,
          gender: response.data.gender,
          department: response.data.department,
          course: response.data.course,
          instructor: response.data.instructor,
          //Periodo inicial a final
          courseDate: response.data.courseDate,
          //Horas del dia
          courseSchedule: response.data.courseSchedule,
          campus: response.data.campus,
          classroom: response.data.classroom,
          group: rowData,
          hours: response.data.hours,
        });
        setLoading((loading) => !loading);
      })
      .catch((error) => {
        props.closePopup(false);
        if (error.response) {
          //Se realizo la peticion y el servidor respondio con un codigo de status
          window.alert('Error: ' + error.response.data.message);
        } else if (error.request) {
          //Se realizo la peticion pero no se recibio una respuesta
          window.alert('Error: ' + error.request.data.message);
        } else {
          //Algo ocurrio al preparar la peticion que desencadeno un error
          console.log('Error: ' + error.message);
        }
        console.log(error.config);
      });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setLoading((loading) => !loading);
    //let url = '/auth/enroll-student'
    axios
      .post(
        '/auth/enroll-student',
        values
      )
      .then((response) => {
        setLoading((loading) => !loading);
        setEnrolled(true);
        setTimeout(() => setRedirect(true), 3000);
      })
      .catch((error) => {
        if (error.response) {
          //Se realizo la peticion y el servidor respondio con un codigo de status
          window.alert('Error: ' + error.response.data.message);
        } else if (error.request) {
          //Se realizo la peticion pero no se recibio una respuesta
          window.alert('Error: ' + error.request.data.message);
        } else {
          //Algo ocurrio al preparar la peticion que desencadeno un error
          console.log('Error: ' + error.message);
        }
        console.log(error.config);
        props.closePopup(false);
      });
  };

  const nextStep = () => {
    setStep(step + 1);
  };

  const prevStep = () => {
    setStep(step - 1);
  };

  const initialForm = () => {
    setStep(step - 3);
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setValues({
      ...values,
      [name]: value,
    });
    //if (validateOnChange) validate({ [name]: value });
  };

  let personalInfoForm = (
    <PersonalInfoForm
      nextStep={nextStep}
      errors={errors}
      setErrors={setErrors}
      values={values}
      handleInputChange={handleInputChange}
    />
  );

  let reviewForm = (
    <ReviewForm
      nextStep={nextStep}
      initialForm={initialForm}
      values={values}
      handleInputChange={handleInputChange}
      closePopup={props.closePopup}
      handleSubmit={handleSubmit}
      loading={loading}
    />
  );

  if (loading) {
    personalInfoForm = <Spinner />;
    reviewForm = <Spinner />;
  }

  if (enrolled) {
    reviewForm = (
      <Alert severity='success' variant='outlined'>
        <AlertTitle>Inscrito correctamente</AlertTitle>
        Redirigiendo a 'Mis cursos'...
      </Alert>
    );
  }

  //Redirige a /mycourses despues de que finalice la petición
  if (redirect) {
    reviewForm = <Redirect to='/mycourses' />;
  }

  switch (step) {
    case 1:
      return personalInfoForm;
    case 2:
      return (
        <StudiesForm
          nextStep={nextStep}
          prevStep={prevStep}
          errors={errors}
          setErrors={setErrors}
          values={values}
          handleInputChange={handleInputChange}
        />
      );
    case 3:
      return (
        <LabourInfoForm
          nextStep={nextStep}
          prevStep={prevStep}
          errors={errors}
          setErrors={setErrors}
          values={values}
          handleInputChange={handleInputChange}
        />
      );
    case 4:
      return reviewForm;
    default:
      console.log('Form');
  }
};

export default EnrollmentForm;
