import { React, Fragment, useState, useLayoutEffect } from 'react';
import Button from '@material-ui/core/Button';
import MaterialTable from 'material-table';
import EnrollmentForm from './EnrollmentForm/EnrollmentForm';
import Popup from '../../components/UI/Popup/Popup';
import Spinner from '../../components/UI/Spinner/Spinner';
import axios from 'axios';

const styles = {
  maxWidth: '90%',
  margin: '1em auto',
};

const CourseEnrollment = (props) => {
  //let url ='/api/course-enrollment'
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);
  const [group, setGroup] = useState([]);
  const [openEnrollment, setOpenEnrollment] = useState(false);

  useLayoutEffect(() => {
    request();
    // eslint-disable-next-line
  }, []);

  const request = () => {
    setLoading((loading) => !loading);
    //let url ='/api/course-enrollment'
    axios
      .get('/api/course-enrollment')
      .then((response) => {
        setData(response.data);
        setLoading((loading) => !loading);
      })
      .catch((error) => {
        setLoading((loading) => !loading);
        if (error.response) {
          //Se realizo la peticion y el servidor respondio con un codigo de status
          window.alert('Error: ' + error.response.data.message);
        } else if (error.request) {
          //Se realizo la peticion pero no se recibio una respuesta
          window.alert('Error: ' + error.request.data.message);
        } else {
          //Algo ocurrio al preparar la peticion que desencadeno un error
          console.log('Error: ' + error.message);
        }
        console.log(error.config);
      });
  };

  let courseEnrollmentTable = (
    <Fragment>
      <Popup
        title='Cedula de inscripcion'
        openPopup={openEnrollment}
        setOpenPopup={setOpenEnrollment}
      >
        <EnrollmentForm rowData={group} closePopup={setOpenEnrollment} />
      </Popup>

      <MaterialTable
        title='Inscribirse a curso'
        columns={[
          { title: 'Curso', field: 'course' },
          { title: 'Lugares restantes', field: 'quota' },
          { title: 'Instructor', field: 'instructor' },
          { title: 'Periodo(d/m/a) y Hora', field: 'period' },
          { title: 'Tipo de capacitación', field: 'trainingType' },
          { title: 'Aula', field: 'classroom' },
          { title: 'Dirigido a', field: 'addressedTo' },
          { title: 'Grupo', field: 'group', hidden: true },
        ]}
        data={data}
        actions={[
          {
            icon: 'save',
            tooltip: 'Save User',
            onClick: (event, rowData) => {
              setGroup(rowData.group);
              setOpenEnrollment((openEnrollment) => !openEnrollment);
            },
          },
        ]}
        components={{
          Action: (props) => (
            <Button
              disabled={props.data.quota == 0 ? true : false}
              onClick={(event) => props.action.onClick(event, props.data)}
              color='primary'
              variant='contained'
              style={{ textTransform: 'none' }}
              size='small'
            >
              Incribirse
            </Button>
          ),
        }}
        options={{
          pageSize: 25,
          pageSizeOptions: [25, 50, 75, 100],
          headerStyle: {
            backgroundColor: '#127826',
            color: '#FFF',
            fontSize: '15px',
            fontWeight: 'bold',
          },
          filterCellStyle: {
            backgroundColor: '#01579b',
            color: '#FFF',
          },
          searchFieldStyle: {
            backgroundColor: '#E8E8E8',
            color: 'black',
          },
        }}
        localization={{
          header: {
            actions: 'Inscribirse',
          },
          toolbar: {
            searchTooltip: 'Buscar',
            searchPlaceholder: 'Buscar',
          },
          pagination: {
            labelDisplayedRows: '{from}-{to} de {count}',
            labelRowsSelect: 'Filas',
            firstAriaLabel: 'Primera pagina',
            firstTooltip: 'Primera pagina',
            previousAriaLabel: 'Pagina anterior',
            previousTooltip: 'Pagina anterior',
            nextAriaLabel: 'Siguiente pagina',
            nextTooltip: 'Siguiente pagina',
            lastAriaLabel: 'Ultima pagina',
            lastTooltip: 'Ultima pagina',
          },
          body: {
            emptyDataSourceMessage: 'No hay cursos disponibles',
          },
        }}
      />
    </Fragment>
  );

  if (loading) {
    courseEnrollmentTable = <Spinner />;
  }

  return <div style={styles}>{courseEnrollmentTable}</div>;
};

export default CourseEnrollment;
