import React from 'react';
import Grid from '@material-ui/core/Grid';
import { Form } from '../../../shared/useForm';
import Button from '@material-ui/core/Button';
import Input from '../../../components/Controls/Input';
import FormHelperText from '@material-ui/core/FormHelperText';
import RadioGroup from '../../../components/Controls/RadioGroup';
import classes from './EnrollmentForm.module.css';

const positionTypeItems = [
  { id: 'Base', title: 'Base' },
  { id: 'Confianza', title: 'Confianza' },
];

const positionLevelItems = [
  { id: 'Funcionario', title: 'Funcionario' },
  { id: 'Enlace', title: 'Enlace' },
  { id: 'Operativo', title: 'Operativo' },
];

const LabourInfoForm = (props) => {
  const { values, errors, setErrors, handleInputChange } = props;
  const {
    positionType,
    positionLevel,
    responsibleUnit,
    area,
    actualPosition,
    immediateBoss,
    address,
    phoneNumber,
    schedule,
  } = values;

  const validate = () => {
    let temp = { ...errors };

    positionType
      ? (temp.positionType = '')
      : (temp.positionType = 'Seleccione un puesto');

    positionLevel
      ? (temp.positionLevel = '')
      : (temp.positionLevel = 'Seleccione un puesto');

    positionType
      ? (temp.positionType = '')
      : (temp.positionType = 'Seleccione un nivel de puesto');

    responsibleUnit
      ? (temp.responsibleUnit = '')
      : (temp.responsibleUnit = 'Seleccione un puesto');

    area ? (temp.area = '') : (temp.area = 'Campo obligatorio');

    actualPosition
      ? (temp.actualPosition = '')
      : (temp.actualPosition = 'Campo obligatorio');

    immediateBoss
      ? (temp.immediateBoss = '')
      : (temp.immediateBoss = 'Campo obligatorio');

    address ? (temp.address = '') : (temp.address = 'Campo obligatorio');

    phoneNumber
      ? /^(\d{10}|\d{11}|\d{12})$/.test(phoneNumber)
        ? (temp.phoneNumber = '')
        : (temp.phoneNumber = 'Número debe contener entre 10 y 12 dígitos')
      : (temp.phoneNumber = 'Campo obligatorio');

    schedule ? (temp.schedule = '') : (temp.schedule = 'Campo obligatorio');

    setErrors({
      ...temp,
    });

    if (values) return Object.values(temp).every((x) => x == '');
  };

  const next = (e) => {
    e.preventDefault();
    props.nextStep();
  };
  const prev = (e) => {
    e.preventDefault();
    props.prevStep();
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (validate()) {
      next(e);
    }
  };

  let form = (
    <Form className={classes.form} onSubmit={handleSubmit}>
      <h4>Informacion laboral</h4>
      <Grid container>
        <Grid item xs={6}>
          <RadioGroup
            name='positionType'
            label='Tipo de puesto'
            value={positionType || ''}
            onChange={handleInputChange}
            items={positionTypeItems}
          />
          <FormHelperText error>{errors.positionType}</FormHelperText>

          <RadioGroup
            name='positionLevel'
            label='Nivel de puesto'
            value={positionLevel || ''}
            onChange={handleInputChange}
            items={positionLevelItems}
          />
          <FormHelperText error>{errors.positionLevel}</FormHelperText>

          <Input
            name='responsibleUnit'
            placeholder='Unidad responsable'
            label='Unidad responsable'
            value={responsibleUnit || ''}
            onChange={handleInputChange}
            inputProps={{ maxLength: 50 }}
            error={errors.responsibleUnit}
          />
          <Input
            name='area'
            placeholder='Area'
            label='Area'
            value={area || ''}
            onChange={handleInputChange}
            inputProps={{ maxLength: 50 }}
            error={errors.area}
          />
          <Input
            name='actualPosition'
            placeholder='Posicion actual'
            label='Posicion actual'
            value={actualPosition || ''}
            onChange={handleInputChange}
            inputProps={{ maxLength: 50 }}
            error={errors.actualPosition}
          />
        </Grid>
        <Grid item xs={6}>
          <Input
            name='immediateBoss'
            placeholder='Jefe inmediato'
            label='Jefe inmediato'
            value={immediateBoss || ''}
            onChange={handleInputChange}
            inputProps={{ maxLength: 100 }}
            error={errors.immediateBoss}
          />
          <Input
            name='address'
            placeholder='Domicilio oficial'
            label='Domicilio oficial'
            value={address || ''}
            onChange={handleInputChange}
            inputProps={{ maxLength: 100 }}
            error={errors.address}
          />
          <Input
            name='phoneNumber'
            placeholder='Telefono oficial'
            label='Telefono oficial'
            type='number'
            value={phoneNumber || ''}
            onChange={handleInputChange}
            error={errors.phoneNumber}
            inputProps={{
              onWheel: (event) => event.currentTarget.blur(),
              min: 0,
            }}
          />
          <Input
            name='schedule'
            placeholder='Horario'
            label='Horario'
            value={schedule || ''}
            onChange={handleInputChange}
            inputProps={{ maxLength: 50 }}
            error={errors.schedule}
          />
        </Grid>
      </Grid>
      <Button
        type='submit'
        fullWidth
        variant='contained'
        color='primary'
        style={{ marginTop: '15px' }}
        onClick={prev}
      >
        Regresar
      </Button>
      <Button
        type='submit'
        fullWidth
        variant='contained'
        color='primary'
        style={{ marginTop: '15px' }}
      >
        Siguiente
      </Button>
    </Form>
  );

  return <div>{form}</div>;
};

export default LabourInfoForm;
