import React from 'react';
import Grid from '@material-ui/core/Grid';
import { Form } from '../../../shared/useForm';
import Button from '@material-ui/core/Button';
import Input from '../../../components/Controls/Input';
import classes from './EnrollmentForm.module.css';

const PersonalInfoForm = (props) => {
  const { values, errors, setErrors, handleInputChange } = props;
  const {
    name,
    firstLastName,
    secondLastName,
    rfc,
    email,
    plaza,
    gender,
    department,
  } = values;
  const validate = () => {
    let temp = { ...errors };

    name ? (temp.name = '') : (temp.name = 'Campo obligatorio');

    firstLastName
      ? (temp.firstLastName = '')
      : (temp.firstLastName = 'Campo obligatorio');

    secondLastName
      ? (temp.secondLastName = '')
      : (temp.secondLastName = 'Campo obligatorio');

    rfc ? (temp.rfc = '') : (temp.rfc = 'Campo obligatorio');

    email ? (temp.email = '') : (temp.email = 'Campo obligatorio');

    plaza ? (temp.plaza = '') : (temp.plaza = 'Campo obligatorio');

    gender ? (temp.gender = '') : (temp.gender = 'Campo obligatorio');

    department
      ? (temp.department = '')
      : (temp.department = 'Campo obligatorio');

    setErrors({
      ...temp,
    });

    if (values) return Object.values(temp).every((x) => x == '');
  };

  const next = (e) => {
    e.preventDefault();
    props.nextStep();
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (validate()) {
      next(e);
    }
  };

  let form = (
    <Form className={classes.personalInfo} onSubmit={handleSubmit}>
      <h4>Datos personales</h4>
      <p>
        Si requiere cambiar datos personales diríjase a su&nbsp;
        <a href='/profile-config'>configuración de perfil</a>
      </p>
      <Grid container>
        <Grid item xs={6}>
          <Input
            disabled
            name='name'
            placeholder='Nombre(s)'
            label='Nombre(s)'
            value={name}
            onChange={handleInputChange}
            error={errors.name}
          />
          <Input
            disabled
            name='firstLastName'
            placeholder='Apellido paterno'
            label='Apellido paterno'
            value={firstLastName}
            onChange={handleInputChange}
            error={errors.firstLastName}
          />
          <Input
            disabled
            name='secondLastName'
            placeholder='Apellido materno'
            label='Apellido materno'
            value={secondLastName}
            onChange={handleInputChange}
            error={errors.secondLastName}
          />
          <Input
            disabled
            name='rfc'
            placeholder='RFC'
            label='RFC'
            value={rfc}
            onChange={handleInputChange}
            error={errors.rfc}
          />
        </Grid>
        <Grid item xs={6}>
          <Input
            disabled
            name='email'
            placeholder='Email'
            label='Email'
            value={email}
            onChange={handleInputChange}
            error={errors.email}
          />
          <Input
            disabled
            name='plaza'
            placeholder='Plaza'
            label='Plaza'
            value={plaza}
            onChange={handleInputChange}
            error={errors.plaza}
          />
          <Input
            disabled
            name='gender'
            placeholder='Genero'
            label='Genero'
            value={gender}
            onChange={handleInputChange}
            error={errors.gender}
          />
          <Input
            disabled
            name='department'
            placeholder='Departamento'
            label='Departamento'
            value={department}
            onChange={handleInputChange}
            error={errors.department}
          />
        </Grid>
      </Grid>
      <Button
        style={{ marginTop: '15px' }}
        type='submit'
        variant='contained'
        color='primary'
      >
        Siguiente
      </Button>
    </Form>
  );

  return <div>{form}</div>;
};

export default PersonalInfoForm;
